@charset "UTF-8";

@font-face {
    font-family: 'pct';
    src: url('./../../../assets/fonts/pct.eot');
    src: url('./../../../assets/fonts/pct.eot?#iefix') format('embedded-opentype'), url('./../../../assets/fonts/pct.woff') format('woff'),
        url('./../../../assets/fonts/pct.ttf') format('truetype'), url('./../../../assets/fonts/pct.svg#pct') format('svg');
    font-weight: normal;
    font-style: normal;
}

[data-icon]:before {
    font-family: 'pct' !important;
    content: attr(data-icon);
    font-style: normal !important;
    font-weight: normal !important;
    font-variant: normal !important;
    text-transform: none !important;
    speak: none;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

[class^='pct-']:before,
[class*=' pct-']:before {
    font-family: 'pct' !important;
    font-style: normal !important;
    font-weight: normal !important;
    font-variant: normal !important;
    text-transform: none !important;
    speak: none;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.pct-arrow1:before {
    content: '\61';
}

.pct-arrow2:before {
    content: '\62';
}

.pct-arrow3:before {
    content: '\63';
}

.pct-arrow4:before {
    content: '\64';
}

.pct-chat1:before {
    content: '\65';
}

.pct-chat2:before {
    content: '\66';
}

.pct-chat3:before {
    content: '\67';
}

.pct-chat4:before {
