}

.pct-arrow-sharp1:before {
    content: '\6a';
}

.pct-arrow-sharp2:before {
    content: '\6b';
}

.pct-arrow-sharp3:before {
    content: '\6c';
}

.pct-arrow-sharp4:before {
    content: '\6d';
}
